.product {
  &__photo {
    width: 100%;
    margin-bottom: 32px;

    @include hover(transform,scale(1.2));
  }

  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    margin: 0 0 8px;
  }

  &__description {
    margin: 0 0 16px;
  }

  &__price {
    margin: 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
  }
}
